import withLayout from '../components/Layout';
import Link from 'next/link';
import mockData from '../data/currencies';

const renderCurrencyRow = (currency) => {
    return (<tr>
        <th scope="row">{currency.rank}</th>
        <td>
            <div className="d-flex align-items-center">
                <img width={16} height={16} src={`https://images.openmarketcap.com/coin_logos/16x16/${currency.global_id}.png`} />
                <Link href={`/currencies/${currency.symbol}`}>
                    {currency.name}
                </Link>
            </div>
        </td>
        <td>{parseFloat(currency.market_cap).toFixed(2)}</td>
        <td>{parseFloat(currency.price_usd).toFixed(3)}</td>
        <td>{parseFloat(currency.volume_usd).toFixed(2)}</td>
        <td>{currency.available_supply}</td>
        <td>{parseFloat(currency.price_change).toFixed(2)}</td>
    </tr>);
};

const Page = () => {
    const currencies = mockData.data;

    return (
        <table className="table">
            <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col">Name</th>
                <th scope="col">Market Cap</th>
                <th scope="col">Price USD</th>
                <th scope="col">Volume (24h)</th>
                <th scope="col">Available supply</th>
                <th scope="col">Price change</th>
            </tr>
            </thead>
            <tbody>
                {currencies.map(renderCurrencyRow)}
            </tbody>
        </table>
    );
}

export default withLayout(Page);
